@import url('https://fonts.googleapis.com/css2?family=Savate:ital,wght@0,200..900;1,200..900&display=swap');

.bar{
    width: 100%;
    height: 50px;
    /* background-color: lightcoral; */
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 0 16px;
    position: relative;
}

.bar h1{
    font-family: "Savate", sans-serif;
    cursor: default;
}

.bar input{
    position: absolute;
    right: 12px;
    top: 50%;
    transform: translateY(-50%);
    z-index: 1;
    width: 160px;
    height: 35px;
    border: 0;
    outline: 0;
    border-radius: 24px;
    background-color: transparent;
    border: 1px solid black;
    box-sizing: border-box;
    padding-left: 12px;
    padding-right: 36px;
}
.bar input::placeholder{
    color: rgba(0, 0, 0, .4);
}

.bar svg{
    /* cursor: pointer; */
    position: relative;
    z-index: 5;
}