li{
    width: 100%;
    height: 70px;
    /* background-color: lightgreen; */
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 0 20px;
    cursor: pointer;
}
li:hover{
    background-color: rgba(0, 0, 0, .05);
}

li .song{
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.song .albumImg{
    width: 50px;
    height: 50px;
    /* background-color: lightsalmon; */
    background-color: rgba(255, 255, 255, 0.4);
    margin-right: 12px;
}
.albumImg img{
    width: 100%;
}
.song .names{
    height: 45px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}

.names h3, .names p{
    text-wrap: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    text-align: left;
    max-width: 240px;
}

li .opt{
    display: flex;
    align-items: center;
    justify-content: center;
}
.opt .now{
    width: 30px;
    height: 30px;
    /* background-color: lightseagreen; */
    margin-right: 8px;
}
.opt .love{
    width: 30px;
    height: 30px;
}