.list{
    width: 100%;
    height: 350px;
    overflow: hidden;
}
.list ul{
    list-style: none;
    width: 100%;
    height: 350px;
    overflow-y: scroll;
    scrollbar-width: none;
}

.noresult{
    width: 100%;
    height: 70px;
    color: rgba(0, 0, 0, .6);
    display: flex;
    align-items: center;
    justify-content: center;
    font-style: italic;
}