.playing{
    width: 100%;
    aspect-ratio: 1/1;
    /* background-color: lightcyan; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
}
.playing .img{
    width: 200px;
    height: 200px;
    background-color: rgba(255,255,255,.3);
    margin-bottom: 20px;
}
.img img{
    width: 100%;
}
.playing .info{
    margin-bottom: 20px;
    text-align: center;
    width: 300px;
    overflow: hidden;
    white-space: nowrap;
    position: relative;
    cursor: default;
}
.info h2{
    /* padding-left: 100%;
    animation: marq 8s linear infinite; */
}

@keyframes marq {
    from {
        transform: translate(0%);
    }
    to {
        transform: translate(-200%);
    }
}

.info h3{
    opacity: .6;
}
.playing .btns{
    width: 100%;
    height: 60px;
    /* background-color: lightpink; */
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    position: absolute;
    bottom: 8px;
    left: 0;
}
.btns .playbtn{
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.btns .like{
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btns svg{
    cursor: pointer;
}

.btns .off{
    fill: rgba(0, 0, 0, .3);
}